<template>
    <div class="page-container">
        <el-aside class="page-nav-left">
            <div class="aside-bar">
                <el-menu default-active="1">
                    <router-link to="/farming/task">
                        <el-menu-item index="0">
                            <i class="el-icon-date"></i>
                            <span slot="title">任务管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/farming/seed">
                        <el-menu-item index="1">
                            <i class="el-icon-coin"></i>
                            <span slot="title">种子管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/farming/tour">
                        <el-menu-item index="2">
                            <i class="el-icon-truck"></i>
                            <span slot="title">巡园管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/farming/work">
                        <el-menu-item index="3">
                            <i class="el-icon-notebook-1"></i>
                            <span slot="title">工作汇报</span>
                        </el-menu-item>
                    </router-link>
                </el-menu>
            </div>
        </el-aside>
        <el-main class="m-l-220 m-t-20 bg-white">
            <div class="flex flex-align-center flex-pack-justify">
                <div class="flex flex-align-center">
                    <el-input class="m-r-15" size="small" style="width:200px;" placeholder="请输入种子编码/名称" suffix-icon="el-icon-search" v-model="vm.searchKey"></el-input>
                    <el-button type="primary" size="small" @click="search">搜索</el-button>
                </div>
                <el-button type="primary" size="small" @click="add">添加种子</el-button>
            </div>
            <el-row class="m-t-30">
                <el-col :span="24">
                    <el-table :height="tableHeight" :data="dataList" size="small" border stripe style="width: 100%">
                        <el-table-column fixed prop="seedNo" label="种子编码"></el-table-column>
                        <el-table-column fixed prop="seedName" label="种子名称"></el-table-column>
                        <el-table-column prop="spec" label="规格"></el-table-column>
                        <el-table-column prop="unit" label="单位"></el-table-column>
                        <el-table-column prop="quantity" label="采购数量"></el-table-column>
                        <el-table-column prop="price" label="采购单价"></el-table-column>
                        <el-table-column prop="total" label="采购金额"></el-table-column>
                        <el-table-column prop="supplier" label="供应商"></el-table-column>
                        <el-table-column prop="time" label="采购时间" width="100"></el-table-column>
                        <el-table-column prop="address" label="采购地址" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="contact" label="联系人"></el-table-column>
                        <el-table-column prop="contactTel" label="联系电话"></el-table-column>
                        <el-table-column fixed="right" label="操作" width="140">
                            <template slot-scope="scope">
                                <el-button type="primary" size="mini" icon="el-icon-search" @click="edit(scope.row)"></el-button>
                                <el-button type="danger" size="mini" icon="el-icon-delete" @click="remove(scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row class="m-t-30">
                <el-col>
                    <el-pagination 
                        class="text-right" 
                        background 
                        layout="total, prev, pager, next" 
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="vm.currentPage"
                        :page-size="vm.pageSize"
                        :total="vm.pageTotal">
                    </el-pagination>
                </el-col>
            </el-row>
            <el-dialog title="种子" :visible.sync="dialogVisible" width="800px" :close-on-click-modal="false">
                <el-form ref="form" size="small" :rules="rules" :model="form" label-width="150px">
                    <el-form-item label="种子编码" prop="seedNo">
                        <el-input style="width:260px;" v-model="form.seedNo"></el-input>
                    </el-form-item>
                    <el-form-item label="种子名称" prop="seedName">
                        <el-input style="width:260px;" v-model="form.seedName"></el-input>
                    </el-form-item>
                    <el-form-item label="规格" prop="spec">
                        <el-input style="width:260px;" v-model="form.spec"></el-input>
                    </el-form-item>
                    <el-form-item label="单位" prop="unit">
                        <el-input style="width:260px;" v-model="form.unit"></el-input>
                    </el-form-item>
                    <el-form-item label="采购数量" prop="quantity">
                        <el-input style="width:260px;" v-model="form.quantity"></el-input>
                    </el-form-item>
                    <el-form-item label="采购单价" prop="price">
                        <el-input style="width:260px;" v-model="form.price"></el-input>
                    </el-form-item>
                    <el-form-item label="采购金额" prop="total">
                        <el-input style="width:260px;" v-model="form.total"></el-input>
                    </el-form-item>
                    <el-form-item label="供应商" prop="supplier">
                        <el-input style="width:260px;" v-model="form.supplier"></el-input>
                    </el-form-item>
                    <el-form-item label="采购时间" prop="time">
                        <el-date-picker style="width:260px;" type="date" placeholder="选择日期" v-model="form.time"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="采购地址" prop="address">
                        <el-input style="width:260px;" v-model="form.address"></el-input>
                    </el-form-item>
                    <el-form-item label="联系人" prop="contact">
                        <el-input style="width:260px;" v-model="form.contact"></el-input>
                    </el-form-item>
                    <el-form-item label="联系电话" prop="contactTel">
                        <el-input style="width:260px;" v-model="form.contactTel"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button size="small" @click="dialogVisible = false">取消</el-button>
                    <el-button size="small" type="primary" @click="save">保存</el-button>
                </span>
            </el-dialog>
        </el-main>
    </div>
</template>

<script>
export default {
    data() {
        return {
            vm: {
                searchKey: '',
                currentPage: 1,
                pageSize: 10,
                pageTotal: 100,
            },
            dataList: [{
                seedNo: '001',
                seedName: '葡萄种子',
                spec: '规格',
                unit: '斤',
                quantity: '100',
                price: '58',
                total: '5800',
                supplier: '江苏扬州智谷',
                time: '2019-08-25',
                address: '江苏省扬州市扬子江中路188号',
                contact: '张女士',
                contactTel: '15869563625',
            }, {
                seedNo: '002',
                seedName: '玉米种子',
                spec: '规格',
                unit: '斤',
                quantity: '100',
                price: '58',
                total: '5800',
                supplier: '江苏扬州智谷',
                time: '2019-08-25',
                address: '江苏省扬州市扬子江中路188号',
                contact: '张女士',
                contactTel: '15869563625',
            }, {
                seedNo: '003',
                seedName: '苹果种子',
                spec: '规格',
                unit: '斤',
                quantity: '100',
                price: '58',
                total: '5800',
                supplier: '江苏扬州智谷',
                time: '2019-08-25',
                address: '江苏省扬州市扬子江中路188号',
                contact: '张女士',
                contactTel: '15869563625',
            }],
            dialogVisible: false,
            form:{
                seedNo: '',
                seedName: '',
                spec: '',
                unit: '',
                quantity: '',
                price: '',
                total: '',
                supplier: '',
                time: '',
                address: '',
                contact: '',
                contactTel: '',
            },
            rules: {
                seedNo: [
                    { required: true, message: '请填写种子编码', trigger: 'blur' },
                ],
                seedName: [
                    { required: true, message: '请填写种子名称', trigger: 'blur' }
                ],
                quantity: [
                    { required: true, message: '请填写采购数量', trigger: 'blur' }
                ],
                price: [
                    { required: true, message: '请填写采购单价', trigger: 'blur' }
                ],
                total: [
                    { required: true, message: '请填写采购金额', trigger: 'blur' }
                ],
                supplier: [
                    { required: true, message: '请填写供应商', trigger: 'blur' }
                ],
                time: [
                    { required: true, message: '请选择采购时间', trigger: 'change' }
                ],
                address: [
                    { required: true, message: '请填写采购地址', trigger: 'blur' }
                ],
                contact: [
                    { required: true, message: '请填写联系人', trigger: 'blur' }
                ],
                contactTel: [
                    { required: true, message: '请填写联系电话', trigger: 'blur' }
                ]
            }
        }
    },
    computed:{
        tableHeight() {
            return document.documentElement.clientHeight - 275;
        }
    },
    methods:{
        search(){

        },
        add(){
            this.dialogVisible = true
        },
        edit(row) {
            this.resetForm()
            this.dialogVisible = true;
            this.form = row
        },
        save(){
            this.$refs['form'].validate((valid) => {
                if (valid) {
                    console.log(this.form)
                    this.dialogVisible = false
                } else {
                    this.$message.error("请检查您的输入！");
                    return false;
                }
            });
        },
        remove(row){
            this.$confirm('是否删除？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => {
                          
            });
        },
        resetForm() {
            this.form = {
                seedNo: '',
                seedName: '',
                spec: '',
                unit: '',
                quantity: '',
                price: '',
                total: '',
                supplier: '',
                time: '',
                address: '',
                contact: '',
                contactTel: '',
            }
        },
        handleSizeChange(val) {
            console.log(val);
        },
        handleCurrentChange(val) {
            console.log(val);
        }
    }
}
</script>

<style>

</style>


